var _ = require('underscore');
var user = require('user');
var Config = require('config');
var env = Config.env[Config.scheme];

/*
 获得一个字符串的字节数
 */
String.prototype.bytes = function() {
    var strLength = 0;
    for (var i = 0; i < this.length; i++) {
        if (this.charAt(i) > '~') strLength += 2;
        else strLength += 1;
    }
    return strLength;
};

/*
 JSON返回的日期类型转换为字符串
 */
String.prototype.toDate = function(format) {
    var d = new Date();
    d.setTime(this.match(/\d+/)[0]);
    return (!!format) ? d.format(format) : d;
};


/*
 日期格式化
 */
Date.prototype.format = function(format) {
    var o = {
        "M+": this.getMonth() + 1,
        "d+": this.getDate(),
        "h+": this.getHours(),
        "m+": this.getMinutes(),
        "s+": this.getSeconds(),
        "q+": Math.floor((this.getMonth() + 3) / 3),
        "S": this.getMilliseconds()
    };

    if (/(y+)/.test(format)) {
        format = format.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
    }

    for (var k in o) {
        if (new RegExp("(" + k + ")").test(format)) {
            format = format.replace(RegExp.$1, RegExp.$1.length == 1 ? o[k] : ("00" + o[k]).substr(("" + o[k]).length));
        }
    }
    return format;
};


var gobalAjax =  {
    jsonp: function(url, data, okFn, errFn, UseToken) {
        // var token = window.localStorage.getItem('accessToken') || '';
        if (_.isObject(data)) {
            data = _.extend({
                deviceinfo: '{"aid":"30001001"}'
            }, data);
            if (UseToken) {
                data['access_token'] = 'web-' + user.getToken();
            }
        }
        if (data.constructor.name == 'String') {
            data = 'deviceinfo={"aid":"30001001"}&' + data;
            if (UseToken) {
                data += '&access_token=' + 'web-' + user.getToken();
            }
        }
        $.ajax({
            data: data,
            method: 'GET',
            jsonp: 'callback',
            dataType: 'jsonp',
            url: env.url_prefix + url,
            success: okFn,
            error: errFn
        });
    },
    get: function(url, data, okFn, errFn) {
        data = _.extend({
            deviceinfo: '{"aid":"10102002","uid":"28a0daf7414b7424e9d69b8deabfeddc"}'
        }, data);

        $.ajax({
            data: data,
            method: 'GET',
            // jsonp: 'callback',
            // dataType: 'jsonp',
            url: env.url_prefix + url,
            success: okFn,
            error: errFn
        });
    },
    /**
     * 获取随机数
     * @param max   最大值
     * @param min   最小值
     * @returns {Number}
     */
    getRandom: function(max, min) {
        max = max || 1000;
        min = min || 0;
        return parseInt(Math.random() * (max - min) + min);
    }
};

module.exports = gobalAjax;



